
.list-item {

	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

	display: block;
	position: relative;
	padding: 9px 10px 9px 40px;
	border-top: 1px solid #EEEEEE;
	border-bottom: 1px solid #EEEEEE;
	margin-bottom: -1px;
	@include clearfix;

	.col {
		width: 10%;
		min-height: 1px;
		float: left;
	}

	&.selected {
		background: #FFF39C !important;
	}

	&:not(.header):hover {
		background: #EEEEEE;
		cursor: pointer;
	}

	&.header {
		font-size: 10px;
		color: #CCCCCC;
		text-transform: uppercase;
		border-top: 0;
		padding-bottom: 2px;
	}


	&.track {

		.col {
			&.name,
			&.artists,
			&.album {
				width: 30%;
			}
		}

		.select-state {
			position: absolute;
			top: 12px;
			left: 15px;
			font-size: 10px;
		}

		&.playing {
			font-weight: 600;
		}
	}


	&.playlist {

		.col {
			&.name {
				width: 30%;
			}
			&.owner {
				width: 20%;
			}
		}
	}
}

.artist-list {
	.artist {
		display: block;
		border: 0;
		margin-bottom: 15px;
		@include clearfix;

		.thumbnail {
			width: 50px;
			margin-right: 20px;
			float: left;
			border: 3px solid transparent;
		}

		.name {
			display: inline-block;
			padding-top: 15px;
		}

		&:hover {
			.thumbnail {
				border-color: $turquoise;
				opacity: 0.8;
			}
		}
	}
}


