input[type="submit"].confirming:hover,
input[type="submit"].destructive:hover {
  background: #b62828;
}

.button.timing-out,
button.timing-out,
input[type="button"].timing-out,
input[type="submit"].timing-out {
  position: relative;
}

.button.timing-out::before,
button.timing-out::before,
input[type="button"].timing-out::before,
input[type="submit"].timing-out::before {
  animation-name: fill_from_left;
  animation-duration: 2s;
  animation-timing-function: linear;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 0;
  background: rgba(255, 255, 255, 0.15);
}

